/* Apply styles to the container */
.container {
  max-width: 800px;
  margin: 0 auto;
  padding: 20px;
  display: flex;
  flex-direction: column;
  align-items: center;
  /* Center content horizontally */
}

/* Apply styles to the tools section */
.tools {
  margin-top: 40px;
}

/* Apply styles to the section title */
.section-title {
  font-size: 24px;
  margin-bottom: 10px;
  color: #333;
}

/* Apply styles to the task list */
.task-list {
  list-style: none;
  padding: 0;
}

/* Apply styles to the task item */
.task-item {
  display: flex;
  align-items: center;
  padding: 10px;
  border-bottom: 1px solid #eee;
  width: 100%;
  /* Ensure tasks take full width */
}

/* Apply styles to the completed task item */
.task-item.completed {
  text-decoration: line-through;
  color: #999;
}

/* Apply styles to the filter buttons */
.filter {
  margin-top: 20px;
}

.filter button {
  margin-right: 10px;
  background-color: #333;
  color: #fff;
  padding: 8px 12px;
  border: none;
  border-radius: 4px;
  cursor: pointer;
  transition: background-color 0.3s ease;
}

.filter button:hover {
  background-color: #555;
}

/* Apply styles to the pre-made list buttons */
.premade-lists {
  margin-top: 20px;
}

.premade-lists button {
  margin-right: 10px;
  background-color: #007bff;
  color: #fff;
  padding: 8px 12px;
  border: none;
  border-radius: 4px;
  cursor: pointer;
  transition: background-color 0.3s ease;
}

.premade-lists button:hover {
  background-color: #0056b3;
}